body, html, #root {
  margin: 0;
  height: 100%;
  font-family: 'Segoe UI', sans-serif;
}

.layout {
  display: flex;
  height: 100vh;
}

.sidebar {
  width: 220px;
  background: #343a40;
  color: white;
  padding: 1rem;
}

.sidebar h3 {
  margin-top: 0;
}

.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar li {
  margin: 1rem 0;
}

.sidebar button {
  width: 100%;
  background: #495057;
  border: none;
  padding: 0.5rem;
  color: white;
  border-radius: 4px;
  cursor: pointer;
}

.sidebar button:hover {
  background: #6c757d;
}

.main-content {
  flex: 1;
  padding: 2rem;
  overflow-y: auto;
  background: #f8f9fa;
}
